* {
  vertical-align: baseline;
  font-weight: inherit;
  font-family: inherit;
  font-style: inherit;
  font-size: 100%;
  border: 0 none;
  outline: 0;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: normal;
  font-size: 100%;
}

ul, ol {
  list-style: none;
}

:focus {
  outline: 0;
}

:link, :visited {
  text-decoration: none;
}

a {
  text-decoration: none;
}

a img {
  border: 0;
}

article, aside, figcaption, figure, footer, header, hgroup, nav, section, time {
  display: block;
}

address, dl, ol, ul, p, pre {
  margin: 0;
}

sub, sup {
  display: inline-block;
}